import {
  api,
} from './index'
import request from './request'
// 登陆
export const login = params => api(request.auth, 'GET', params)
export const wxlogin = params => api(request.wxConfig, 'GET', params)
//wx.config error 更新 失效时间
export const setJsapiTiem = params => api(request.setJsapiTiem, 'GET', params)
// 获取用户信息
export const userInfo = params => api(request.userInfo, 'GET', params)
export const wxPermission = params => api(request.auth, 'GET', params)
