<template>
  <div class="addSign">
    <div class="container">
      <van-cell-group :border="false">
        <van-field
          v-model="EventsData.companyName"
          label="客户"
          type="text"
          input-align="right"
          placeholder="请输入"
          :error="false"
          :border="false"
          :readonly="true"
        />

        <div class="select-list">
          <div class="list">
            <div class="left">
              所属项目
            </div>
            <div class="right">
              <select
                v-model="EventsData.projectName"
                v-placeholder="{color: EventsData.projectName}"
                :disabled="OTP"
              >
                <option
                  v-for="(item,idx) in pgList"
                  :key="idx"
                  :value="item.information.projectName"
                >
                  {{ item.information.projectName }}
                </option>
              </select>
            </div>
            <div
              v-sanjiao="{color: EventsData.projectName}"
              class="saojiao"
            />
          </div>
        </div>

        <div
          v-if="upType!=2"
          class="select-list"
        >
          <div
            class="list"
            @click="startTimeClick()"
          >
            <div class="left">
              时间
            </div>
            <div
              class="right"
              :class="{'selectPlaceholder':EventsData.signingTime== ''}"
            >
              {{ EventsData.signingTime== '' ? '请选择' : EventsData.signingTime }}
            </div>
            <div class="timeBox">
              <span
                class="iconfont iconrili"
                style="color:#aebab4"
              />
            </div>
          </div>
        </div>
        <van-field
          v-else
          v-model="EventsData.signingTime"
          label="时间"
          
          input-align="right"
          placeholder="请输入"
          :error="false"
          :border="false"
          :readonly="upType==2"
        />
        <div class="money-text">
          <van-field
            v-model="EventsData.contractAmount"
            style="width:90%;"
            label="合约金额"
            type="number"
            input-align="right"
            placeholder="请输入合约金额 单位:"
            :error="false"
            :border="false"
            :readonly="upType==2"
          />
          <span class="right-text">万元</span>
        </div>

        <van-field
          v-model="EventsData.remark"
          label="备注"
          type="textarea"
          input-align="right"
          placeholder="请输入"
          rows="1"
          autosize
          :border="false"
          :readonly="upType==2"
        />

        <div
          v-if="upType!=2"
          class="select-map"
        >
          <div class="uploadFile">
            <span class="iconfont iconshangchuan uploadIcon" />
            <input
              class="file"
              name="file"
              type="file"
              @change="update"
            >
            <div class="uploadText">
              点击上传附件
            </div>
          </div>
        </div>
        <div style="width: 50%;overflow: hidden;margin-left: 10px;">
          {{ newFile }}
        </div>
        <div
          v-if="upType!=2"
          class="select-list"
        >
          <div
            v-if="workType!=3"
            class="list"
          >
            <div class="left">
              关联拜访
            </div>
            <div class="right">
              <select
                v-model="EventsData.visitId"
                v-placeholder="{color: EventsData.visitId}"
                :disabled="OTP"
              >
                <option value="">
                  否
                </option>
                <option value="1">
                  是
                </option>                
              </select>
            </div>
            <div
              v-sanjiao="{color: EventsData.visitId}"
              class="saojiao"
            />
          </div>
        </div>
        <add-visit
          v-if="EventsData.visitId==1&&upType!=2"
          :is-c-d-t="isCDT"
          :is-c-p-t="true"
          @VisitChange="watchVisit"
        />
      </van-cell-group>

      <div class="footer">
        <van-button
          v-if="upType!=2"
          class="gradual-btn"
          style="width:100%;"
          type="primary"
          @click="addSet"
        >
          确认信息
        </van-button>
      </div>
    </div>

    <!--  时间选择 -->
    <van-popup
      v-model="selectStartTime"
      position="bottom"
      :overlay="true"
    >
      <van-datetime-picker
        v-model="currentTime"
        type="date"        
        :min-date="minDate"
        :formatter="formatter"
        @confirm="startconfirm($event)"
        @cancel="startconfirm()"
      />
    </van-popup>
  </div>
</template>
<script>
// import { api, get, post } from "@/service";
import { mapState } from 'vuex'
import { formatDate, isNull } from '@/utils'
import { addProject,projectList,idProject,upload } from '@/service/customer'
import addVisit from '../addVisit/addVisit.vue'
export default {
  name: 'AddSign',
  // components: {
  //   'add-visit': addVisit,
  // },
  props:{
    workType:{
      type:Number,
      default:null
    },
    upIng: {
      type: Boolean,
      default: false
    },
    //事件标识
    isNo:{
      type:Number ,
      default:1
    },
  },
  data() {
    return {
      project: -1, // 所属项目
      price: '', // 合约金额
      memo: '', // 备注
      visitIndex: -1, // 关联拜访
      selectStartTime: false,
      maxDate: new Date(),
      minDate: new Date(1900, 1, 1),
      currentTime: new Date(),
      EventsData: {
        projectName: '', // 项目名称
        contractAmount: '', // 合同金额
        remark: '', // 备注
        visitId: '', // 关联拜访Id
        signingTime: '', // 签约时间（yyyy-MM-dd）
        tuneUpEnclosure: '', // 上传附件的URL
        projectId:'',
      },
      pgList:[],
      upType:1,
      isCDT:false,
      visitDTO:null,
      newFile:'',
      OTP:false,//当前打开该详情的状态
    }
  },
  computed: {
    ...mapState(['companyName']),
  },
  watch:{
    upIng(newV,oldV){
      console.log('upIng',newV,oldV)
      this.pgList.map(d=>{
        this.EventsData.projectId = d.information.projectName == this.EventsData.projectName ? d.id: this.EventsData.projectId
      })
      let txts = {                                
        contractAmount:'合同金额',        
        signingTime:'签约时间',  
      }       
      if (!this.$checkFrom(this.EventsData,txts)) {
        return
      }
      let params = {
        information:JSON.stringify(this.EventsData),
        "menuType":5,
		    "companyId":this.$route.query.id,
		    "projectId":this.EventsData.projectId,
		    "userId":this.$store.getters.userInfo.id,
        "userName":this.$store.getters.userInfo.name,
        isNo:this.isNo
      }
      this.$emit('upedChange',params)
      
      return
    },
  },
  mounted() { },

  created() { 
    this.getProjectList()
    if(this.$route.query.id){
      this.getIdProject(this.$route.query.id)
      this.upType =2
      this.OTP=true//当前打开该详情的状态
    }
    
    this.EventsData.companyName=this.$store.getters.userData.companyName
    this.EventsData.companyId= this.$store.getters.userData.companyId
  },
  methods: {
    //确定事件
    addSet(){      
      if(this.EventsData.visitId==1){
        this.isCDT = !this.isCDT
      }else{
        console.log('签约签约签约')
        this.clickAddEvents()
      }
    },
    //监听回收数据
    watchVisit(e){
      console.log('监听回收数据',e)
      this.visitDTO = e
      this.clickAddEvents()
    },
    //根据id获取目标详情
    getIdProject(id){
      idProject({id:id}).then(res=>{
        // console.log('idProject',res)
        this.EventsData = res.information
      })
    },
    getProjectList() {
      const params = {
        menuType: 1,
        size:1000
      }
      projectList(params).then((res) => {
        console.log('projectList', res)
        this.pgList = res.data
        if(res.data.length>0){
          if(this.EventsData.projectName){
            return
          }
          this.EventsData.projectName = res.data[0].information.projectName
        }
      })
    },
    clickAddEvents() {
      const EventsData = {}
      this.pgList.map(d=>{
        this.EventsData.projectId = d.information.projectName == this.EventsData.projectName ? d.id: this.EventsData.projectId
      })
      let txts = {                                
        contractAmount:'合同金额',        
        signingTime:'签约时间',  
      }       
      if (!this.$checkFrom(this.EventsData,txts)) {
        return
      }
      EventsData.information = JSON.stringify(this.EventsData) 
      EventsData.menuType = 5
      EventsData.projectId = this.EventsData.projectId
      EventsData.visitDTO = this.visitDTO
      console.log('签约签约签约')
      addProject(EventsData).then(res=>{
        console.log(res)
        if(res.text=="error"){
          this.$notify(res.message)
          return 
        }
        this.$router.go(-1)
      })
    },
    formatter(type, value) {
      if (type === 'year') {
        return `${value}年`
      } if (type === 'month') {
        return `${value}月`
      }
      return `${value}日`
    },
    startTimeClick() {
      this.selectStartTime = true
    },
    startconfirm(e) {
      // 选开始时间
      if (isNull(e)) {
        this.selectStartTime = false
      } else {
        const time = formatDate(new Date(e), 'yyyy-MM-dd', 'special')
        const timeStr = time.split('-')
        this.EventsData.signingTime = `${timeStr[0]}-${timeStr[1]}-${timeStr[2]}`
        this.selectStartTime = false
      }
    },
    update(e) {
      let file = e.target.files[0]
      let param = new FormData()
      param.append('file', file)
      let userData = this.$store.getters.userData
      let fileName = userData.companyAbbreviation+'-'+userData.companyBusinessLicense+'-'+ formatDate(new Date(),'yyyyMMdd')
      let config = {
        headers: { 'Content-Type': 'multipart/form-data' }
      }
      upload(param, config)
        .then(res => {
          console.log('上传文件', res.data)
          this.$notifySty('上传成功')
          this.newFile = res.data.url
          this.EventsData.fileName = fileName
          this.EventsData.fileUrl = res.data.url
        })
    }
  },
}
</script>
