<template>
  <div id="app">    
    <router-view />
  </div>
</template>
<script>
import { wxPermission, userInfo } from '@/service/login'
import { mapState, mapMutations } from 'vuex'
import { homeList, powerList, idPowerList,} from '@/service/home'
export default {
  data() {
    return {
      userNo: '',
      userId:null,
      OTarrType: [true, true],
      OTarrName: ['企业列表', '动态列表'],
    }
  },
  created() {
    // if(!window.sessionStorage.getItem('fristLogin')){
    //   window.sessionStorage.setItem('fristLogin',true)
    //   this.replace('/')
    // }
    if(window.location.hostname == 'localhost'){
    //测试
      this.userNo = '21313'
      this.getUserInfo()
    }else{
      //正式
      this.init()
    }
  },
  methods: {
    ...mapMutations(['SET_USER_INFO']),
    init() {
      wxPermission({
        agentId: 7,
        url: window.location.href,
        // state:'YiGeShenChenman'
      }).then((res) => {
        console.log('wxPermission登录1',res)
        //正式代码
        this.userNo = this.GetQueryString('userNo')        
        if (this.userNo) {          
          this.getUserInfo()
        } else {
          //正式代码
          window.location.href = res.data
        }
      })
        .catch(err => console.error(err))
    },
    
    getUserInfo() {
      userInfo({ userNo: this.userNo }).then((res) => {
        console.log('获取用户信息getUserInfo', res)
        this.SET_USER_INFO(res.data)
        this.userId = res.data.id
        this.getIdPowerList()
      })
    },
    GetQueryString(name) {
      const reg = new RegExp(`(^|&)${name}=([^&]*)(&|$)`)
      const r = window.location.search.substr(1).match(reg)
      if (r != null) return unescape(r[2])
      return ''
    },
        // id获取权限
    getIdPowerList() {
      idPowerList({ id: this.userId }).then((res) => {
        
        this.$store.commit('SET_LIST_POWER', res)
        // eslint-disable-next-line array-callback-return
        this.OTarrName.map((d, idx) => {
          this.OTarrType[idx] = res.OTarr.indexOf(d) > -1
        })
        console.log('getIdPowerList',this.OTarrType)

        ///获取权限
        this.$store.commit('SET_HOME_POWER', this.OTarrType)
        // this.
      })
    },
  },
}
</script>
