              alt=""
            >
          </div> 
        </div> -->
        
        <div class="select-list">
          <div class="list">
            <div class="left">
              拜访方式
            </div>
            <div class="right">
              <select
                v-model="form.visitMode"
                v-placeholder="{color: form.visitMode}"
                :disabled="OTP"
                @change="visitModeChange(form.visitMode)"
              >
                <option value="">
                  请选择
                </option>
                <option value="电话">
                  电话
                </option>
                <option value="现场">
                  现场
                </option>
                <option value="其他">
                  其他
                </option>
              </select>
            </div>
            <div
              v-sanjiao="{color: form.visitMode}"
              class="saojiao"
            />
          </div>
        </div>
        
        <div
          v-show="form.visitMode=='现场'"
          id="container"
          style="width:100%; height:300px"
          class="body"
        />
        <iframe
          id="geoPage"
          width="0"
          height="0"
          frameborder="0"
          style="display:none;"
