          :up-ing="upIng"
          :is-no="2"
          @callBack="constCallBack"
        />
      </van-cell-group>
      <!-- <div> <div v-if="form.visitMode=='现场'">xy</div> 
        <div>getx{{ form.visitLongitude }}</div>
        <div>gety{{ form.visitLatitude }}</div>
        <div>$storex{{ $store.getters.indexXY.longitude }}</div>
        <div>$storey{{ $store.getters.indexXY.latitude }}</div>
      </div> -->
      <div class="footer">
        <van-button
          v-if="!$route.query.OTP||$route.query.OTP==1"
          class="gradual-btn"
