<script>
import { addVisit, putVisit,upload, getVisit, idProject, menuProject, projectList } from '@/service/customer'
// import { mapState } from 'vuex'
import addConst from '../addConst/addConst.vue'
import { txMap } from '@/txMap.js'
import { formatDate, isNull } from '@/utils'

export default {
  name: 'AddVisit',
  components: {
    'add-const': addConst,
  },
  props: {
    //iscomponent
    isCPT: {
      type: Boolean,
      default: false
    },
    //回收数据isCallDataType
    isCDT: {
      type: Boolean,
      default: false
    },
  },
  data() {
    return {
      form: {
        visitName: this.$store.getters.userInfo.name,
        visitUserId: this.$store.getters.userInfo.id,
        companyName: this.$store.getters.userData.companyName,
        visitType: '',
        visitContent: '',
        visitMode: '',
        visitSigned: '',
        visitTime: '',
        projectDTOS: [],
        projectId: '',
        visitLatitude:'',
        visitLongitude:'',
      },
      callLists: [{
        name: '立项',
        val: '1'
      }, {
        name: '报价',
        val: '2'
      }, {
        name: '尽调',
        val: '3'
      }, {
        name: '上会',
        val: '4'
      }, {
        name: '签约',
        val: '5'
      },],
      callList: [],
      setIsType1: '0',//事件1关联的事件id
      isType1D: {},
      setIsType2: '0',//事件2关联的事件id
      isType2D: {},
      selectStartTime: false,
      maxDate: new Date(),
      minDate: '',
      currentTime: new Date(),
      upType: 1,//关联菜单标识
      upIng: false,//回收数据通讯标识
      uped: false,//回收数据态
      pgList: [],
      OTP:false,//当前打开该详情的状态
      imgList:[],//上传图片
    }
  },
  watch: {
    isCDT(newV, oldV) {
      this.form.userId = this.$store.getters.userInfo.id
      this.form.userName = this.$store.getters.userInfo.name
      let params = this.form
      let txts = {
        visitType:'拜访类型',
        visitContent:'拜访内容',
        visitMode:'拜访方式',
        visitTime:'拜访时间',
      } 
      if(!this.$checkFrom(params,txts)){return}
      this.$emit('VisitChange', this.form)
      return
    }
  },
  mounted(){
    let that = this            
  },
  updated(){
    let container = document.getElementById('container')
    if(container.children[0]){
      container.children[0].childNodes[1].innerHTML = ''
    }
  },  
  
  created() { 
    const nowDate = new Date(new Date().toLocaleDateString()) // 获取当前时间
    this.minDate = new Date(nowDate.getTime() - 2 * 24 * 60 * 60 * 1000)
    if (this.$route.query.id) {
      this.getVT(this.$route.query.id)
      this.upType = 2,
      this.OTP = this.$route.query.OTP==2||this.$route.query.OTP==1 ? true : false
    }
    this.getProjectList()
    this.indexXY = this.$store.getters.indexXY
  },
  methods: {
    update(e) {
      let file = e.target.files[0]
      let _this = this
      let param = new FormData()
      param.append('file', file)
      let userData = this.$store.getters.userData
      let fileName = userData.companyAbbreviation+'-'+userData.companyBusinessLicense+'-'+ formatDate(new Date(),'yyyyMMdd')
      let config = {
        headers: { 'Content-Type': 'multipart/form-data' }
      }
      upload(param, config)
        .then(res => {
          // console.log('上传文件', res.data,_this.$notifySty)
          // console.log();
          this.$notifySty('上传成功')
          this.newFile = res.data.url
          this.imgList.push(res.data.url)
          this.imgList.length = this.imgList.length > 2 ?  2:this.imgList.length
        })
    },
    mapObj(x,y){
      txMap().then((qq) => {      
      // console.log(qq)
      const map = new qq.maps.Map(document.getElementById('container'), {
        // 地图的中心地理坐标。
        center: new qq.maps.LatLng(x,y),
        zoom: 15,                       //设置缩放级别
        draggable: false,               //设置是否可以拖拽
        scrollwheel: false,             //设置是否可以滚动
        disableDoubleClickZoom: true,    //设置是否可以双击放大
        scaleControl: false,
        mapTypeControlOptions: {
          mapTypeIds: [
          ],
        },//置空地图类型        
        zoomControl: false,  //设置是否可以缩放
      })
        var marker = new qq.maps.Marker({
        //设置Marker的位置坐标
        position: new qq.maps.LatLng(x,y),
        //设置显示Marker的地图
        map: map
      })
      })
    },
    //projectChange项目变更
    projectChange() {
      menuProject({ id: this.form.projectId }).then(res => {
        this.callList = []
        this.callLists.map((d, ) => {
          res.data.map((dd, ) => {
            if (d.val == dd) this.callList.push(d)
          })
        })
        // this.form.projectId = res.data[0].information.projectName
        console.log('projectChange项目变更', res)
      })
    },
    //拜访方式变化
    visitModeChange(itm){
      if(itm=='现场'&&this.$route.query.OTP!=2){
        console.log('run')
        this.form.visitLongitude = this.$store.getters.indexXY.longitude
        this.form.visitLatitude = this.$store.getters.indexXY.latitude
        
        this.mapObj(this.form.visitLatitude,this.form.visitLongitude)
      }
    },
    //获取项目列表
    getProjectList() {
      const params = {
        menuType: 1,
        size:1000
      }
      projectList(params).then((res) => {
        this.pgList = res.data
        if(this.pgList.length>0){
          this.form.projectId = this.pgList[0].id
          this.projectChange()
        }        
      })
    },
    //触发关联菜单
    setIsTypeChange(who) {
      if (who == 1) {
        this.isType1D.isSet = true
      } else {
        this.isType2D.isSet = true
      }
    },
    //获取关联组件的返回值
    constCallBack(e) {
      console.log('获取关联组件的返回值',e)
      this.form.projectDTOS[e.isNo - 1] = e
      this.addVT()
    },    
    clickAdd() {
      let params = this.form
      let txts = {
        visitType:'拜访类型',
        visitContent:'拜访内容',
        visitMode:'拜访方式',
        visitTime:'拜访时间',
      } 
      if(!this.$checkFrom(params,txts)){return}
      this.upIng = !this.upIng
      if (this.$route.query.id) {
        this.putVT()
      } else {
        this.addVT()
      }
    },
    addVT() {
      const params = this.form
      params.visitUserId = this.$store.getters.userInfo.id
      if(this.isType1D.isSet||this.isType2D.isSet){
        // console.log('this.form.projectDTOS',this.form.projectDTOS)
        if(this.form.projectDTOS.length==0)return
      }
      addVisit(params).then((res) => {
        this.$router.go(-1)
      })
    },
    putVT() {
      const params = this.form
      params.visitUserId = this.$store.getters.userInfo.id
      putVisit(params).then((res) => {
        this.$router.go(-1)
      })
    },
    getVT(id) {
      getVisit({ id }).then((res) => {
        this.form = res.data
        console.log(this.form)
        this.mapObj(this.form.visitLatitude,this.form.visitLongitude)
      })
    },
    formatter(type, value) {
      if (type === 'year') {
        return `${value}年`
      } if (type === 'month') {
        return `${value}月`
      }
      return `${value}日`
    },
    startTimeClick() {
      if(this.OTP) return
      this.selectStartTime = true
    },
    startconfirm(e) {
      // 选开始时间
      if (isNull(e)) {
        this.selectStartTime = false
      } else {
        const time = formatDate(new Date(e), 'yyyy-MM-dd', 'special')
        const timeStr = time.split('-')
        this.form.visitTime = ` ${timeStr[0]}-${timeStr[1]}-${timeStr[2]}`
        this.selectStartTime = false
      }
    },
  },
}
</script>
