import axios from 'axios'
import store from '@/store' // 引入store
import Vue from 'vue'

const ajx = axios.create({
  //正式
  // baseURL: 'http://scrm.ocheng.me/v2/api/',
  //v2
  baseURL: 'http://v2.ocheng.me/api/', 
  //stage
  // baseURL: 'http://stage.ocheng.me/api/', 
  // 设定请求环境(测试or生产)，由DefinePlugin 插件定义
  timeout: 36000, // 超时时间
  headers: {
    Accept: 'application/json',
    'X-Channel': 3,
    'X-Channel-Type': 1,
  },
})

ajx.interceptors.request.use(config => config)

ajx.interceptors.response.use(
  (res) => {
    // TODO: 需要修改的返回数据处理
    // console.log('触发response拦截');
    // res.data.
    const {
      code,
      message,
    } = res.data
    // console.log('error1',res)
    // console.log('error2',res.data)
    if (code === 0) {
      return res.data
    }
    res.data = res.data || {}
    res.data.message = res.data.message || '接口返回报错'
    return Promise.reject(res.data)
  },
  (error) => {
    // 这里 不reject 导致 promise变成 resolve 状态
    // console.log('error2',error)
    
    if (error.response) {
      // console.log('error3',error.response)
      return Promise.reject(error.response.data); // eslint-disable-line
    }
    return null
  },
)
// eslint-disable-next-line func-names
const api = function (url, type, params) {
  params = params || {}
  let addUserData = {}
  // console.log('store.getters.userInfo',store.getters.userInfo)
  if (store.getters.userInfo) {
    addUserData.userId= store.getters.userInfo.id
    addUserData.userName= store.getters.userInfo.name||''
  }
  if(store.getters.userData){
    addUserData.companyId= store.getters.userData.companyId||''
    addUserData.companyName= store.getters.userData.companyName||''
  }
  if (params.companyId) delete addUserData.companyId
  if (params.companyName) delete addUserData.companyName
  
  let axiosMain
  if (type.toUpperCase() === 'GET') {    
    params = Object.assign(params, addUserData)
    delete params.companyName
    console.log('params',url,params)
    if(!params.userId&&url=='fuli/permission/list'){
      Vue.prototype.$notify('当前登录状态已经过期')
      console.log('当前登录状态已经过期',params.userId)
      setTimeout(()=>{
        window.open(window.location.host+'/h5/fuli')
      },1000)    
    }
    let id
    if (params.id) {
      // eslint-disable-next-line prefer-destructuring
      id = params.id
      const urls = `${url}/${id}`
      axiosMain = ajx.get(urls)
    } else {
      axiosMain = ajx.get(url, {
        params,
      })
    }
  } else if (type.toUpperCase() === 'POST') {
    params = Object.assign(params, addUserData)
    if(params.notCompanyId) delete params.notCompanyId
    axiosMain = ajx.post(url, params)
  } else if (type.toUpperCase() === 'PUT') {
    params = Object.assign(params, addUserData)
    axiosMain = ajx.put(url, params)
  } else if (type.toUpperCase() === 'OPEN') {
    return window.open(`${url}`)
  } else if (type.toUpperCase() === 'DELETE') {
    params = Object.assign(params, addUserData)
    // console.log('DELETE','params',params);
    axiosMain = ajx.delete(url, {
      params,
    })
  } else if (type.toUpperCase() === 'UPLOAD') {
    params = Object.assign(params, addUserData)        
    params= {
      file:params
    }
    // console.log('params',params)
    axiosMain = ajx({
      url: `${url}`,
      method: 'post',
      data: params,
      // dataType: 'json',
      headers: { //添加请求头        
        "Content-Type": "multipart/form-data"
      }
    })
  } else if (type.toUpperCase() === 'GETLOG') {
    // params = Object.assign(params, addUserData)
    // if(params.notCompanyId) delete params.notCompanyId
    params.userId = addUserData.userId
    params.userName = addUserData.userName
    axiosMain = ajx.post(url, params)
  }  else if (type.toUpperCase() === 'GETCOM') {
    params.userId = addUserData.userId
    params.userName = addUserData.userName
    let id
    if (params.id) {
      // eslint-disable-next-line prefer-destructuring
      id = params.id
      const urls = `${url}/${id}`
      axiosMain = ajx.get(urls)
    } else {
      axiosMain = ajx.get(url, {
        params,
      })
    }
  } 

  
  return axiosMain.then((res) => {
    // console.log('axiosMain返回值 111111111',res )
    if(typeof res.data == 'string'){
      // console.log('axiosMain返回值 string 路径',url )
      return {    
        data: res.data,
      }
    }
    // console.log('axiosMain返回值 2222', typeof res.data )   
    if(typeof res.data == 'object'){
      res.data = res.data || []
      res.data.data = res.data.data || []
    }  
    
    if (res.data.status) {
      // console.log('axiosMain返回值 status 路径 路径',url )
      return {
        status: true,
        msg: res.data.msg,
        data: res.data,
        
      }
    }else if(res.data){
      // console.log('axiosMain返回值 data 路径',url ,'value:',res.data)
      return {
        status: true,
        data: res.data || [],
      }
    }else if(res.code==0){
      // console.log('code==0 路径',url)
      return {    
        data: res.data,
      }
    }else{
      // console.log('return 路径',url)
      return {
        // status: false,
        // text: res.data.message,
        data: res,
      }
    }

  }).catch(err =>   
    {
      console.log(err)
      Vue.prototype.$notify(err.message)
      
      return {
        status: false,
        text: `${err.statusText ? err.statusText : 'error'}`,
        message: err.message,
        code :err.code
      }    
    })
}
export {
  ajx,
  api,
}
