<script>
import { addProject,idProject } from '@/service/customer'
import { mapState } from 'vuex'
import { formatDate,strChange } from '@/utils'
// import { formatDate, isNull } from '@/utils'
export default {
  name: 'AddApproval',
  //workType:1,默认;2,只读;3父组件引用需返回EvaentsData的值
  props:{
    workType: {
      type: Number,
      default: null
    },
    upIng: {
      type: Boolean,
      default: false
    },
    //事件标识
    isNo:{
      type:Number ,
    },
  },
  data() {
    return {      
      EventsData: {
        projectName: '', // 项目名称
        projectAmount: '', // 项目金额
        lessee: '', // 承租人
        // guarantee: '', // 担保人
        // mainBusiness: '', // 主营业务
        useOfFunds: '', // 资金用途
        deliveryMode:'直租',
      },
      upType:1,
      deliveryModeList:['直租','售后回租','其他'],
      OTP:false
    }
  },
  computed: {
    ...mapState(['companyName']),
  },
  watch:{
    upIng(newV,oldV){
      // console.log('upIng',newV,oldV)

      let params = {
        information:JSON.stringify(this.EventsData),
        "menuType":2,
		    "companyId":1,
		    "projectId":15,
		    "userId":1,
        "userName":"quito",
        isNo:this.isNo
      }
      this.$emit('upedChange',params)
      
      return
    },
  },
  mounted() {

  },
  created() {
    if(this.$route.query.id){
      this.getIdProject(this.$route.query.id)
      this.upType =2
      this.OTP=true//当前打开该详情的状态
    }
    // this.workType = this.workType || 1
    this.EventsData.companyName=this.$store.getters.userData.companyName
    this.EventsData.companyId= this.$store.getters.userData.companyId
    const nowDate = new Date(new Date().toLocaleDateString()) // 获取当前时间
    this.minDate = new Date(nowDate.getTime() - 2 * 24 * 60 * 60 * 1000)
  },
  methods:{
    //输入框更新
    iptChange(value){
      let v = strChange(value)      
      if(!v)return
      this.$set(this.EventsData,value.k,v.v)
    },
    //根据id获取目标详情
    getIdProject(id){
      idProject({id:id}).then(res=>{
        console.log('idProject',res.information)
        this.EventsData = res.information
      })
    },
    callBack(){
      this.$emit('addCall',this.EventsData)
    },
    clickAddEvents() {      
      const EventsData = {}
      EventsData.information = JSON.stringify(this.EventsData)       
      EventsData.menuType = 1
      addProject(EventsData).then(res=>{
        if(!res.status){
          this.$notify(res.message)
        }else{
          this.$router.go(-1)
        }
      })
    },
  },
}
</script>
