<script>
// @ is an alias to /src
import { formatDate, isNull } from '@/utils'

export default {
  name: 'HomeSearch',
  props: {
    // eslint-disable-next-line vue/require-default-prop
    tabIndex: {
      type: Number,
    },
    // 是否需要排序按钮
    isSort: {
      type: Boolean,
    },
    // isMoney
    isMoney: {
      type: Boolean,
    },
    // isTime
    isTime: {
      type: Boolean,
    },
    isSearch:{
       type: Boolean,
    },
    notShowSearch:{
       type: Boolean,
    },

  },
  data() {
    return {
      runSortType:false,//是否排序
      visible: false,
      selectStartTime: false,
      selectEndTime: false,
      startDate: new Date(),
      endDate: new Date(),
      searchObj: {
        companyName: null,
        companyIndustry: null,
        startTime: null,
        companyCustomerType: null,
        endTime: null,
        minCreditQuota: null,
        maxCreditQuota: null,
        projectName:null,
        startTime:null,
        endTime:null,
        rentBackType:''
      },

      selectList: [],
      search_item2_text: '',
      rentBackTypeList:[
        { name:'请选择类型',val:''},
        { name:'财务类型',val:0},
        { name:'基础类型',val:1},
        { name:'征信报告',val:2},
        { name:'其他类',val:3},
      ],
    }
  },
  created() {
    this.searchObj.tabIndex = this.tabIndex
    // eslint-disable-next-line no-nested-ternary
    this.search_item2_text = this.tabIndex === 1 ? '客户类型' : this.tabIndex === 2 ? '动作' : '选择类型'
  },
  methods: {
    formatter(type, value) {
      if (type === 'year') {
        return `${value}年`
      } if (type === 'month') {
        return `${value}月`
      }
      return `${value}日`
    },
    StartTimeClick() {
      // 选开始时间触发事件
      this.selectStartTime = true
    },
    EndTimeClick() {
      // 选结束时间触发事件
      this.selectEndTime = true
    },
    startconfirm(e) {
      // 选开始时间
      if (isNull(e)) {
        this.selectStartTime = false
      } else {
        const time = formatDate(new Date(e), 'yyyy-MM-dd')
        this.searchObj.startTime = time
        this.selectStartTime = false
      }
    },
    endconfirm(e) {
      // 选结束时间
      if (isNull(e)) {
        this.selectEndTime = false
      } else {
        const time = formatDate(new Date(e), 'yyyy-MM-dd')
        this.searchObj.endTime = time
        this.selectEndTime = false
      }
    },
    ClearSearchObj() {
      this.searchObj = {
        companyName: null,
        companyIndustry: null,
        startTime: null,
        companyCustomerType: null,
        endTime: null,
        minCreditQuota: null,
        maxCreditQuota: null,
      }
    },
    searchToggle() {
      this.visible = !this.visible
    },
    searchClear() {
      console.log('取消')
      this.visible = !this.visible
      this.ClearSearchObj()
      this.emitSearch('clear')
    },
    search(e) {
      // /搜索事件
      console.log('确定')
      e.preventDefault()
      this.emitSearch('search')
    },
    // 排序
    runSort() {
      this.runSortType = !this.runSortType
      // console.log('runSortType')
      this.$emit('_runSort', this.runSortType)
    },
    emitSearch(type) {
      //  传值事件
      if (type === 'search') {
        this.$emit('postSearch', this.searchObj)
      } else {
        this.$emit('clearSearch', this.tabIndex)
      }
    },
  },
}
</script>
