// homeList 企业列表
export const homeList = params => api(request.homeList, 'GET', params).then(res=>{
  // console.log(res)
  res.data = res.data||[]
  
  return res
})
//GETCOM 企业列表
export const homeLists = params => api(request.homeList, 'GETCOM', params).then(res=>{
  // console.log(res)
  res.data = res.data||[]
  res.data.data.map(d=>{
    d.dayed = null
   if(d.finalFollowUpTime) {
    d.dayed = (new Date().getTime() - new Date(d.finalFollowUpTime).getTime())/60/60/24/1000
    // console.log(typeof d.dayed)
    d.dayed = d.dayed<1 ? 1:Math.ceil(d.dayed)
    
   }
  //  console.log(d)
  })
  return res
})
// 动态列表
export const logList = params => api(request.logList, 'GETLOG', params).then((res) => {
  // console.log('d.动态列表',res)
  let UID = store.getters.userInfo.id+''
  // let UID = '1';
  res.data.operationLogVOS = res.data.operationLogVOS ? res.data.operationLogVOS : []
  res.data.operationLogVOS.sort((a, b) => a - b)
  res.data.operationLogVOS.map((d) => {
    d.isShow = true
    d.createTime = formatDate(new Date(d.createTime) ,'yyyy-MM-dd hh:mm:ss')
    d.messageTime = formatDate(new Date(d.messageTime),'yyyy-MM-dd')
    // console.log('d.browseUsers',d.browseUsers)
    if(d.browseUsers){
      // console.log('d.browseUsers',UID,d.browseUsers.indexOf(UID))
      d.isRead = d.browseUsers.indexOf(UID) > -1 ? true : false
    }        
    return d
  })

  return res
})

// 指定id权限列表
export const idPowerList = params => api(request.idPowerList, 'GET', params).then((res) => {
  // console.log('getListPower', res)
  res.data.PermissionVO = res.data.PermissionVO || []
  const OTarr = []
  const TTarr = []
  // const oneArr = []
  // const twoArr = []
  const OAStr = []
  const TAStr = []

  const PermissionObj={}

  // eslint-disable-next-line array-callback-return
  res.data.PermissionVO.map((d) => {
    if (d.level === 1) {
      OAStr.push(d.name)
    } else {
      TAStr.push(d.name)
    }
    if (d.showH5) {
      if (d.level === 1) {
        OTarr.push(d.name)
      } else {
        TTarr.push(d.name)
      }
    }
    PermissionObj[d.permissionEn] = d.permissionEn
    //   console.log('name',d.name)
  })
  //  console.log('name',PermissionObj)
  return {
    OTarr,
    TTarr,
    OAStr,
    TAStr,
    PermissionObj
  }
})

