<template>
  <div name="dynamic">
    <div class="contact_container">
      <div class="contact">
        <div class="contact">
          <!-- 筛选 -->
          <home-search
            v-if="searchType==1"
            :tab-index="tabIndex"
            :is-sort="true"
            :is-time="true"
            :not-show-search="true"
            @postSearch="getSearch($event)"
            @clearSearch="clearDataSearch($event)"
            @_runSort="runSort"
          />
          <!-- 内容部分 -->
          <div class="contact_container_er">
            <van-list
              v-model="loadingLog"
              :finished="finishedLog"
              :finished-text="finished_text"
              :error.sync="error"
              error-text="请求失败，点击重新加载"
              @load="onLoad()"
            >
              <div
                v-for="(item,idx) in logDList"
                :key="idx"
                class="item-list"
                :class="{'topOne':item.top,}"
              >
                <span
                  v-if="item.top&&notShowList.indexOf(item.createTime.slice(5,7))==-1"
                  class="time"
                  @click="ctrlListShow(item.createTime)"
                >
                  {{ item.createTime.slice(0,7) }}  
                  <span class="plus">+</span>                
                </span>
                
                <span
                  v-if="item.top&&notShowList.indexOf(item.createTime.slice(5,7))>-1"
                  class="time"
                  @click="ctrlListShow(item.createTime,true)"
                >
                  {{ item.createTime.slice(0,7) }}
                  <span class="plus">+</span>
                </span>
                
                <div
                  v-if="item.isShow"
                  class="item-left"
                >
                  <div
                    :class="{'xian':true,'xianTop':item.top,'xianBottom':item.bottom}"
                  />
                  <div :class="{'circular':true,'theme-background-colors':item.top||item.bottom}" />
                </div>
                <div
                  v-if="item.isShow"
                  class="item-right"                  
                  @click="routerTos(item)"
                >
                  <div class="right">
                    <div class="top">
                      <h4 class="text_h4">
                        {{ item.companyName }}
                      </h4>
                      <p class="text_p">
                        动作：{{ item.editType + item.actionType }}
                      </p>
                      <p class="text_p">
                        最后跟进时间：{{ item.createTime }}
                      </p>
                      <p class="text_p">
                        所属项目：{{ item.projectName }}
                      </p>
                      <p class="text_p">
                        操作人：{{ item.operatorName }}
                      </p>
                      <span
                        v-if="PermissionObj['messageboard_insert'] != undefined"
                        style="position:absolute;right:16px;bottom:65px;"
                        class="iconfont iconliuyan theme-colors"
                        @click.stop="addMessage(item)"
                      />
                      <div
                        v-if="item.messageId && PermissionObj['messageboard_list'] != undefined"
                        class="message"
                        @click.stop="showMessage(item)"
                      >
                        <div class="message-left">
                          {{ item.content }}
                        </div>
                        <div
                          class="message-right"
                        >
                          <span>{{ item.messageTime }}</span>
                          <van-icon
                            style="position:relative;top:2px;"
                            name="arrow"
                          />
                        </div>

                        <span
                          v-if="!item.isRead"
                          style="position:absolute;right:0;top:1px;color:#ff0021;"
                          class="iconfont iconnew"
                        />
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </van-list>
          </div>
        </div>
      </div>
    </div>

    <!-- 留言板弹层 -->
    <van-popup v-model="messageBoard">
      <div class="messageBoard">
        <div class="header">
          留言板
        </div>
        <div
          v-for="(item,idx) in messageLists"
          :key="idx"
          class="container"
        >
          <div class="container_header">
            <span style="float:left;">{{ item.author }}</span>
            <span style="float:right;">{{ item.createTime }}</span>
          </div>
          <div class="container_p">
            {{ item.content }}
          </div>
        </div>
      </div>
    </van-popup>

    <!-- 新增留言弹层 -->
    <van-popup v-model="AddmessageBoard">
      <div class="messageBoard">
        <div class="header">
          留言板
        </div>
        <div class="container">
          <textarea
            v-model="messageStr"
            :class="messageError ? 'textarea_no' :'textarea_yes'"
            placeholder="请输入留言"
          />
        </div>
        <van-row>
          <van-col span="12">
            <van-button
              class="clear-btn"
              style="width:100%;"
              square
              type="primary"
              @click="messageClear()"
            >
              取消
            </van-button>
          </van-col>
          <van-col span="12">
            <van-button
              class="confirm-btn"
              style="width:100%;"
              square
              type="primary"
              @click="messageSave"
            >
              确定
            </van-button>
          </van-col>
        </van-row>
      </div>
    </van-popup>
  </div>
</template>
<script>
import { logList,} from '@/service/home'
import { messageList,addMessages } from '@/service/customer'
import { isNull,addMark } from '@/utils'
import HomeSearch from '../Home/Home-search.vue'
import { firmInfo } from '@/service/customer'
export default {
  name: 'Dynamic',
  components: {
    'home-search': HomeSearch,
  },
  props:{
    searchType:{
      type:Number,
      default:null
    }
  },
  data() {
    return {
      messageBoard: false,
      AddmessageBoard: false,
      messageStr: '',
      messageError: false,
      PermissionObj:this.$store.state.powerList.PermissionObj,///权限
      tabIndex: 2,
      searchObj: null,
      pageLog: 1,
      size: 5,
      logDList: [],
      totalLog: null,
      loadingLog: false,
      finishedLog: false,
      finished_text: '没有更多数据啦',
      error: false,
      setingItem:{},//选中某条动态
      setingMessage:{},//选中某条留言
      messageLists:[],//留言列表
      notShowList:[],//隐藏队列      
      notCompanyId:false,//是否含有CompanyId
    }
  },
  computed: {
    
  },
  created() {
    //this.getLogList()// 获取动态
    // console.log('this.$props',this.$props,this.searchType)
    this.notCompanyId = this.searchType==1 ? true : false
  },
  methods: {
    //控制列表显示隐藏
    ctrlListShow(time,type=false){
      
      let mm  = time.substring(5,7)
      let cpLogDList = this.logDList
      console.log('控制列表显示隐藏',this.notShowList)
      let mdx = this.notShowList.indexOf(mm)
      mdx > -1 ? this.notShowList.splice(mdx,1):this.notShowList.push(mm)

      cpLogDList.map(d=>{
        let mmm = d.createTime.substring(5,7)
        if(mmm==mm){
          d.isShow = type
        }        
      })
      this.logDList = cpLogDList
    },
    getData(item) {
      const params = {
        id: item.companyId,
      }
      firmInfo(params).then((res) => {    
        console.log('企业信息firmInfo',res)
        this.$store.commit('SET_USER_DATA', res.data)
        this.routerTos(item)
      })
    },
     //展示留言
    showMessage(item){
      this.messageBoard = !this.messageBoard
      this.setingMessage = item
      item.isRead = true
      this.getMessageList(item)
    },
    //获取留言列表
    getMessageList(item){
      // console.log('notCompanyId',notCompanyId)
      let params = {
        eventType:item.eventType,
        eventId:item.eventId,
        messageId:item.messageId,
        notCompanyId:this.notCompanyId
        // id:item.id,
      }
      
      messageList(params).then(res=>{
        this.messageLists = res.data || []
        // console.log('获取留言',res)
      })
    },
    //动态详情跳转
    routerTos(item){
      if(!this.$store.getters.userData){
        this.getData(item)
        return
      }
      let strArr = this.$store.getters.AarrList
      let routerList = this.$store.getters.routerList
      let query = {
            id:item.eventId,
            OTP:2,
            CID:item.companyId
          }
      // console.log(strArr.indexOf(item.eventType),item.eventType,strArr)
      if(item.eventType=='13')query.Tid = item.eventId
      
      if(strArr.indexOf(item.eventType)>-1){

        this.$router.push({
          path:routerList[strArr.indexOf(item.eventType)],
          query:query
       })
      }      
    },
    // 控制筛选
    getSearch(data) {
      console.log('动态列表控制筛选',data)
      this.searchObj = data      
      let searchType = false // search是否变化
      this.logDList  = []
      this.totalLog = null
      this.loadingLog = true// 加载状态结束
      this.finished_text = ''
      this.pageLog  = 1
      this.onLoad()
    },
    clearDataSearch(data) {
      // 清除筛选数据
      this.getSearch()
    },
    //唤醒留言表单
    addMessage(item) {
      console.log('item',item)
      this.setingItem = item
      this.messageBoard = false
      setTimeout(() => {
        // 加载状态结束
        this.AddmessageBoard = true
      }, 300)
    },
    // 留言板取消事件
    messageClear() {
      
      this.AddmessageBoard = false
      this.messageStr = ''
      this.setingItem = {}
    },
    // 留言保存事件
    messageSave() {
      console.log('留言保存事件',this.messageStr)
      this.setMessage()
      if (isNull(this.messageStr)) {
        this.messageError = true
        return
      }
      
    },
    //新增留言
    setMessage(){      
      let params = {
        author:this.$store.getters.userInfo.name,
        authorUserId:this.$store.getters.userInfo.id,
        eventId:this.setingItem.eventId,
        content:this.messageStr,
        eventType:this.setingItem.eventType
      }
      // console.log('新增留言',params)
      addMessages(params).then(res=>{
        // console.log('获取留言',res)
        this.messageStr = ''
        this.messageError = false     
        this.AddmessageBoard = false
        this.getInit()
        this.onLoad()
      })
    },
    getInit(){
      this.page=1
      this.pageLog = 1
      this.logDList = []
      this.totalLog = null
      this.total = null
    },
    // 加载数据
    onLoad() {
      const that = this
      if (this.logDList.length === this.totalLog) {
        this.finishedLog = true // 数据全部加载完成
        this.loadingLog = false// 加载状态结束
        this.finished_text = '没有更多数据了'
        return
      } 
      setTimeout(() => {
        that.getLogList()
      }, 1000)
    },
    // 获取动态列表
    getLogList() {
      
      let parmes = {...this.searchObj}
      parmes.page =  this.pageLog
      parmes.size =  this.size
      // console.log('获取动态列表parmes',parmes)
      for(let i in parmes){
        parmes[i] === ''?parmes[i] = null:''
        
      }
      let that = this
      // eslint-disable-next-line no-unused-expressions
      if(this.searchType!=1){
        parmes.companyId= this.$store.getters.userData.companyId
        parmes.companyName= this.$store.getters.userData.companyName
      }
      // console.log('获取留言parmes',parmes)
      logList(parmes).then((res) => {
        console.log('getLogList', res)
        this.loadingLog = false// 加载状态结束
        this.totalLog = res.data.count
        if(res.data.operationLogVOS.length==0||!res.data.operationLogVOS){
          return
        }
        if (this.logDList.length > 0) {
          delete this.logDList[(this.pageLog - 1) * (this.size) - 1].bottom
        }
        let mdxList = this.notShowList
        console.log('mdxList',mdxList)
        // if(mdxList.length>0){
        //   mdxList.map(mm=>{
        //     res.data.operationLogVOS.map(dd=>{
        //       let mmm = dd.createTime.substring(5,7)
        //       if(mmm==mm){
        //         dd.isShow = !dd.isShow
        //       }  
        //     })                
        //   })
        // }        
        this.logDList = this.logDList.concat(res.data.operationLogVOS)
        that.logDList = addMark(that.logDList,3)        
        
        this.pageLog += 1
        
      })
    },
    //排序
    runSort(e){
       this.logDList = this.logDList.reverse()
    }
    
  },
}
</script>
