import Vue from 'vue'
import Vant, { Toast, } from 'vant'
import { wxlogin,setJsapiTiem } from '@/service/login'
import App from './App.vue'
import router from './router'
import store from './store'
import {  isNull,isWeiXin, } from '@/utils'
import 'vant/lib/index.css'
import './assets/css/reset.css'
// eslint-disable-next-line import/extensions
import request from './service/request.js'
// 引入自定义组件。index.js是组件的默认入口
// import visit from './components/visit/visit'
import addVisit from '@views/addVisit/addVisit'

Vue.component('add-visit', addVisit)
Vue.use(Vant)

Vue.config.productionTip = false


Vue.directive('placeholder', {
  inserted(el) {
    el.className = 'selectPlaceholder'
  },
  update(el, binding) {
    if (binding.value.color === -1) {
      el.className = 'selectPlaceholder'
    } else {
      el.className = ' '
    }
  },
})
Vue.directive('sanjiao', {
  inserted(el) {
    el.className += ' selectborder'
  },
  update(el, binding) {
    if (binding.value.color === -1) {
      el.className += ' selectborder'
    } else {
      el.classList.remove('selectborder')
    }
  },
})

// 跳转前更新
router.beforeEach((to, from, next) => {
  Toast.clear()
  // console.log(to, from)
  
  next()
})


// 全局路由跳转后监听
router.afterEach((to, from) => { 
  
  // console.log(window.location.href ,1111)
  // 微信签名
  // TODO: 判断是否为微信环境
  if(to.name=='home'){
    store.commit('SET_ACTIVE',0)
    store.commit('SET_USER_DATA',null)
  }
  
  setTimeout(()=>{
    const isWX = isWeiXin() // TODO: 判断是否为微信环境
  if (isWX) {
    // console.log(window.location.href ,2222)
    wxlogin({
        url: window.location.href
      })
      .then((res) => {
        console.log('jsapi', res)
        window.wx.config({
          beta: true,
          debug: false,
          appId: res.data.appid,
          timestamp: res.data.timestamp,
          nonceStr: res.data.nonceStr,
          signature: res.data.signature,
          jsApiList: ['openLocation',
            'getLocation',
            'openEnterpriseChat',
            'invoke'
          ]
        })
        window.wx.ready(() => {   
          // console.log('获取外部联系人',wx.invoke)     
          // console.log('openEnterpriseChat回话接口',wx.openEnterpriseChat)  
          //  TODO: 设置功能
          wx.getLocation({
            type: 'gcj02', // 默认为wgs84的gps坐标，如果要返回直接给openLocation用的火星坐标，可传入'gcj02'
            success: function (res) {
              // alert('jssdk获取当前定位33', res)
              // console.log('jssdk获取当前定位33', res)
              var latitude = res.latitude // 纬度，浮点数，范围为90 ~ -90
              var longitude = res.longitude // 经度，浮点数，范围为180 ~ -180。
              var speed = res.speed // 速度，以米/每秒计
              var accuracy = res.accuracy // 位置精度
              let indexXY = {
                latitude: res.latitude,
                longitude: res.longitude,
              }
              store.commit('SET_INDEXXY', indexXY)
              // console.log('indexXY',store.getters.indexXY)
            }
          })
        })
        window.wx.error((res) => {
          //确保一层登录态只能触发一次清除缓存          
          if(store.getters.iswx_remove){
            return
          }
          setJsapiTiem().then((res) => {
            store.commit('SET_ISWX_REMOVE', true)
            wxlogin({
              url: window.location.href
            })
          }).catch(err => console.error(err))
        })
      })
      .catch(err => console.error(err))
    }
  },1000)
  

})
// 自定义this原型链方法
/**
 * methods:$Clog
 * 输出
 */
Vue.prototype.$Clog = res => console.log(res)
Vue.prototype.$isNull = (str) => {
  if (str === '' || str === undefined || str === null) {
    return true
  }
  return false
}
// eslint-disable-next-line func-names
Vue.prototype.$checkFrom = function (data, from) {
  // console.log(data, from)
  let type = true  
  //eslint-disable-next-line no-restricted-syntax
  for (let i in from) {
    if (isNull(data[i])) {
      console.log(i, data[i])
      this.$notify(`${from[i]}为必填项`)
      type = false
    }
  }
  return type
}
// eslint-disable-next-line func-names
Vue.prototype.$baseUrl = function () {
  //正式
  // return 'http://scrm.ocheng.me/v2/api/storage/download?remoteFilename='
  //测试
  return 'http://v2.ocheng.me/api/storage/download?remoteFilename='
}

Vue.prototype.$notifySty = function (str, fontsize, bgd) {
  str = str || '提示'
  fontsize = fontsize || 1000
  bgd = bgd || 'green'
  this.$notify({
    message: str,
    duration: fontsize,
    background: bgd,
  })
}
new Vue({
  router,
  store,
  render: h => h(App),
}).$mount('#app')
