            :error="false"
            :border="false"
            :readonly="upType==2"
          />
          <span class="right-text">万元</span>
        </div>
        <div class="money-text">
          <van-field
            v-model="EventsData.serviceCharge"
            style="width:90%;"
            label="服务费"
            type="number"
            input-align="right"
            placeholder="请输入服务费 单位:"
            :error="false"
            :border="false"
            :readonly="upType==2"
          />
          <span class="right-text">元</span>
        </div>
        <div class="money-text">
