          :border="false"
          :readonly="upType==2"
        />
        <van-field
          v-model="EventsData.pledge"
          label="抵质押（多个逗号隔开）"
          type="text"
          input-align="right"
          placeholder="请输入抵质押（多个逗号隔开）"
          :error="false"
          :border="false"
          :readonly="upType==2"
        />        
        <div
          v-if="upType!=2"
          class="select-map"
        >
          <div class="uploadFile">
            <span class="iconfont iconshangchuan uploadIcon" />
            <input
              class="file"
              name="file"
              type="file"
              @change="update"
            >
            <div class="uploadText">
              点击上传附件
            </div>
          </div>
