              关联拜访
            </div>
            <div class="right">
              <select
                v-model="EventsData.visitId"
                v-placeholder="{color: EventsData.visitId}"
                :disabled="OTP"
              >
                <option value="">
                  否
                </option>
                <option value="1">
                  是
                </option>                
              </select>
            </div>
            <div
              v-sanjiao="{color: EventsData.visitId}"
              class="saojiao"
            />
          </div>
        </div>
        <add-visit
          v-if="EventsData.visitId==1"
          :is-c-d-t="isCDT"
          :is-c-p-t="true"
          @VisitChange="watchVisit"
        />
      </van-cell-group>

      <div class="footer">
        <van-button
          v-if="upType!=2&&upType!=2"
          class="gradual-btn"
          style="width:100%;"
          type="primary"
          @click="addSet"
