<script>
// import { api, get, post } from "@/service";
import { mapState } from 'vuex'
import { formatDate, isNull,strChange } from '@/utils'
import { addProject,projectList,idProject,upload  } from '@/service/customer'
import addVisit from '../addVisit/addVisit.vue'
export default {
  name: 'AddCommerce',
  // components: {
  //   'add-visit': addVisit,
  // },
  //workType:1,默认;2,只读;3父组件引用需返回EvaentsData的值
  props:{
    workType:{
      type:Number,
      default:null
    },
    upIng: {
      type: Boolean,
      default: false
    },
    //事件标识
    isNo:{
      type:Number ,
      default:1
    },
  },
  data() {
    return {
      project: -1, // 所属项目
      rentalRates: '', // 租赁利率
      deposit: '', // 保证金
      servicePrice: '', // 服务费
      earnings: '', // 项目内部收益率
      visitIndex: -1, // 关联拜访,
      EventsData: {
        projectName: '', // 项目名称
        totalProjectCreditQuota: '', // 项目授信总额度
        remark: '', // 备注
        rentaInterestRate: '', // 租赁利率
        visitId: '', // 关联拜访Id
        bond: '', // 保证金
        upperAirControl: '', // 上传附件的URL
        serviceCharge: '', // 服务费
        projectId:'',
        internalRateOfReturn:'',//内部收益率
        leaseTerm:'',//租赁期限
        payMode:'',//支付方式
        guarantorName:'',//保证人名称（多个逗号隔开）
        pledge:''//抵质押（多个逗号隔开）
      },
      upType:1,
      pgList:[],
      isCDT:false,
      visitDTO:null,
      newFile:'',
      OTP:false,//当前打开该详情的状态
    }
  },
  computed: {
    ...mapState(['companyName', 'projectCredit']),
  },
  watch:{
    upIng(newV,oldV){
      // console.log('upIng',newV,oldV)
      this.pgList.map(d=>{
        this.EventsData.projectId = d.information.projectName == this.EventsData.projectName ? d.id: this.EventsData.projectId
      })
      let txts = {  
        pledge:'抵质押',   
        guarantorName:'保证人名称',    
        payMode: '支付方式',
        leaseTerm: '租赁期限',                      
        rentaInterestRate: '租赁利率', 
        bond: '保证金',     
        serviceCharge: '服务费', 
        internalRateOfReturn: '内部收益率',                      
        totalProjectCreditQuota:'项目授信总额度',                       
      }       
      if (!this.$checkFrom(this.EventsData,txts)) {
        return
      }
      let params = {
        information:JSON.stringify(this.EventsData),
        "menuType":4,		    
		    "companyId":this.$route.query.id,
		    "projectId":this.EventsData.projectId,
		    "userId":this.$store.getters.userInfo.id,
        "userName":this.$store.getters.userInfo.name,
        isNo:this.isNo
      }
      this.$emit('upedChange',this.information)
      
      return
    },
  },
  mounted() { },

  created() {
    this.getProjectList()
    if(this.$route.query.id){
      this.getIdProject(this.$route.query.id)
      this.upType =2
      this.OTP=true//当前打开该详情的状态
    }        
    this.EventsData.companyName=this.$store.getters.userData.companyName
    this.EventsData.companyId= this.$store.getters.userData.companyId
   },
  methods: {
    //输入框更新
    iptChange(value){
      let v = strChange(value)      
      if(!v)return
      this.$set(this.EventsData,value.k,v.v)
    },
    //确定事件
    addSet(){      
      if(this.EventsData.visitId==1){
        this.isCDT = !this.isCDT
      }else{
        this.clickAddEvents()
      }
    },
    //监听回收数据
    watchVisit(e){
      // console.log('监听回收数据',e)
      this.visitDTO = e
      this.clickAddEvents()
    },
    //根据id获取目标详情
    getIdProject(id){
      idProject({id:id}).then(res=>{
        // console.log('idProject',res)
        
        this.EventsData = res.information
      })
    },
    getProjectList() {
      const params = {
        menuType: 1,
        size:1000
      }
      projectList(params).then((res) => {
        // console.log('projectList', res)
        this.pgList = res.data
        if(res.data.length>0){
          if(this.EventsData.projectName){
            return
          }
          this.EventsData.projectName = res.data[0].information.projectName
        }
      })
    },
    clickAddEvents() {
      const EventsData = {}
      let txts = {  
        pledge:'抵质押',   
        guarantorName:'保证人名称',    
        payMode: '支付方式',
        leaseTerm: '租赁期限',                      
        rentaInterestRate: '租赁利率', 
        bond: '保证金',     
        serviceCharge: '服务费', 
        internalRateOfReturn: '内部收益率',                      
        totalProjectCreditQuota:'项目授信总额度',                       
      }       
      if (!this.$checkFrom(this.EventsData,txts)) {
        return
      }
      this.pgList.map(d=>{
        this.EventsData.projectId = d.information.projectName == this.EventsData.projectName ? d.id: this.EventsData.projectId
      })
      EventsData.information = JSON.stringify(this.EventsData) 
      EventsData.menuType = 4
      EventsData.projectId = this.EventsData.projectId
      EventsData.visitDTO = this.visitDTO
      addProject(EventsData).then(res=>{
        this.$router.go(-1)
      })
    },
    formatter(type, value) {
      if (type === 'year') {
        return `${value}年`
      } if (type === 'month') {
        return `${value}月`
      }
      return `${value}日`
    },
    startTimeClick() {
      this.selectStartTime = true
    },
    startconfirm(e) {
      // 选开始时间
      if (isNull(e)) {
        this.selectStartTime = false
      } else {
        const time = formatDate(new Date(e), 'yyyy-MM-dd', 'special')
        const timeStr = time.split('-')
        this.dateTime = `${timeStr[2]}/${timeStr[1]}/${timeStr[0]}`
        this.selectStartTime = false
      }
    },    
    update(e) {
      let file = e.target.files[0]
      let userData = this.$store.getters.userData
      let fileName = userData.companyAbbreviation+'-'+userData.companyBusinessLicense+'-'+ formatDate(new Date(),'yyyyMMdd')
      let param = new FormData()
      param.append('file', file)
      // console.log(param.get('file'))
      let config = {
        headers: { 'Content-Type': 'multipart/form-data' }
      }
      upload(param, config)
        .then(res => {
          // console.log('上传文件', res.data)
          this.$notifySty('上传成功')
          this.newFile = res.data.url
          
          this.EventsData.fileUrl = res.data.url
          this.EventsData.fileName = fileName
        })
    }
  },
}
</script>
