<template>
  <div class="home">
    <!-- <div>ISWX:{{ isWX }}</div> -->
    <van-tabs
      v-model="active"
      color="#03C061"
      sticky
      swipeable
    >
      <van-tab
        v-if="OTarrType[0]"
        title="企业列表"
      >
        <div class="contact">
          <!-- 筛选-->

          <home-search
            :tab-index="tabIndex"
            :is-sort="true"
            :is-money="true"
            @postSearch="getSearch($event)"
            @clearSearch="clearDataSearch($event)"
            @_runSort="runSort($event)"
          />

          <!-- 内容部分 -->
          <div class="contact_container">
            <div class="container">
              <div class="header">
                共{{ total }}条客户数据
              </div>
              <van-list
                v-model="loading"
                :finished="finished"
                :finished-text="finished_text"
                :error.sync="error"
                error-text="请求失败，点击重新加载"
                @load="onLoad()"
              >
                <div
                  v-for="(item,idx) in homeListData"
                  :key="idx"
                  class="item-list"
                  @click="goCustomer(item.companyId)"
                >
                  <h4 class="text_h4">
                    {{ item.companyName }}
                  </h4>
                  <p class="text_p">
                    {{ item.companyIndustry }}
                  </p>
                  <!-- <p class="text_p">
                    {{ item.dayed }}天未跟进
                  </p> -->
                  <p class="text_p">
                    最后跟进时间：{{ item.finalFollowUpTime }}
                  </p>
                  <p class="text_p">
                    最后操作人：{{ item.operatorName }}
                  </p>
                  <p class="text_p">
                    最后事件：{{ item.actionType }}
                  </p>
                </div>
              </van-list>
            </div>

            <!-- 录入新客户 -->
            <div
              v-if="$store.state.powerList.PermissionObj['concatinfo_insert'] != undefined"
              class="contact_bottom"
            >
              <van-button
                class="btn gradual-btn"
                type="primary"
                @click="routerTo"
              >
                录入新客户
              </van-button>
            </div>
          </div>
        </div>
      </van-tab>

      <!-- ///////第二页///////////////////// -->
      <van-tab
        v-if="OTarrType[1]"
        title="动态列表"
      >
        <tab-dynamic
          v-if="OTarrType[1]&&active==1"
          :search-type="1"
        />
      </van-tab>
    </van-tabs>
  </div>
</template>

<script>
// @ is an alias to /src
import { isNull,addMark } from '@/utils'
import HomeSearch from './Home-search.vue'
import tabDynamic from '../customerManagement/tab-dynamic.vue'
import { homeList, powerList, idPowerList, logList,homeLists} from '@/service/home'
import { messageList,addMessages } from '@/service/customer'
import { setTimeout, clearTimeout } from 'timers'
var t = null

export default {
  name: 'Home',
  components: {
    'home-search': HomeSearch,
    'tab-dynamic':tabDynamic
  },
  data() {
    return {
      active:0,
      tabIndex: 1,
      loading: false,
      finished: false,
      finished_text: '没有更多数据啦',
      PermissionObj:this.$store.state.powerList.PermissionObj,///权限
      error: false,
      searchObj: {},
      cpSearchObj: {},
      messageBoard: false,
      AddmessageBoard: false,
      messageStr: '',
      messageError: false,
      homeListData: [],
      page:1,
      size:5,
      total: null,
      loadType: false,
      PreviousPage: 0,
      OTarrType:['1','2'],
      OTarrName: ['企业列表', '动态列表'],
      isWX: '',
      logDList: [],
      pageLog: 1,
      totalLog: null,
      finishedLog: false,
      loadingLog: false,
      setingItem:{},//选中某条动态
      setingMessage:{},//选中某条留言
      MessageList:[],//留言列表
    }
  },
  watch:{
    active(newV,oldV){
      // console.log('active','n=',newV,'o=',oldV)
      this.$store.commit('SET_HOME_ACTIVE',newV)
      return 
    }
  },
  created() {      
    this.active = this.$store.getters.homeActive
  },
  mounted() {
    let that = this         
    this.PermissionObj = this.$store.state.powerList.PermissionObj
    
  }, 
  destroyed() {    
  },
  updated(){
    this.getOTarrType()
  },
  methods: {  
     //获取当前home权限  
    getOTarrType(){           
      let OTarrTypes = this.$store.getters.homePower          
      if(OTarrTypes){
        this.OTarrType = OTarrTypes
        // console.log('OTarrTypes',OTarrTypes)
        return true
      }else{
        this.getOTarrType()
        return false     
      }      
    },
    // 加载数据
    onLoad() {
      const that = this
      if (this.tabIndex === 1) {
        if (this.homeListData.length === this.total) {
          this.finished = true // 数据全部加载完成
          this.loading = false// 加载状态结束
          return
        }
      }
      // 下拉加载事件
      if (that.tabIndex === 1) {
        setTimeout(() => {
          that.getHomeData()
        }, 1000)
      }
    },
         
    // 获取数据
    getHomeData() {
      let parmes = {...this.searchObj}
      parmes.page = this.page
      parmes.size = this.size
      // console.log('parmes', parmes)
      homeLists(parmes).then((res) => {
        res.data.data = res.data.data ? res.data.data : []
        this.homeListData = this.homeListData.concat(res.data.data)
        this.total = res.data.total
        // eslint-disable-next-line no-plusplus
        this.page++
        this.loading = false// 加载状态结束
      })
    },
    formatter(type, value) {
      if (type === 'year') {
        return `${value}年`
      } if (type === 'month') {
        return `${value}月`
      }
      return value
    },
    goCustomer(id) {
      ///权限处理
      if(isNull(this.$store.state.powerList.PermissionObj['compaylist_list'])){
        return
      }
      this.$router.push({
        path: '/customer',
        query: {
          id,
        },
      })
    },
    
    // 控制筛选
    getSearch(data) {
      // console.log('动态列表控制筛选',data)
      this.searchObj = data      
      let searchType = false // search是否变化
      this.homeListData = []
        this.page = 1
        this.total = null
        this.loading = true
      this.finished_text = ''
      this.pageLog  = 1
      this.onLoad()
    },
    // 控制筛选    
    clearDataSearch(data) {
      // 清除筛选数据
      this.getSearch()
      
    },
    addMessage(item) {
      // console.log('item',item)
      this.setingItem = item
      this.messageBoard = false
      setTimeout(() => {
        // 加载状态结束
        this.AddmessageBoard = true
      }, 300)
    },
    messageClear() {
      // 留言板取消事件
      this.AddmessageBoard = false
      this.messageStr = ''
      this.setingItem = {}
    },
    routerTo() {
      this.$router.push('/addCustomer')
      // to=
    },        
    //排序
    runSort(e){
       this.homeListData = this.homeListData.reverse()
    }
  },
}
</script>
